@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@100;300;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 0px;
  width: 100vw;
  height: 100vh;
  background: rgb(128, 34, 217);
  background: linear-gradient(
    16deg,
    rgba(128, 34, 217, 1) 0%,
    rgba(200, 155, 255, 1) 100%
  );
}

.App {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.three-canvas {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-filter: drop-shadow(-200px 290px 130px rgba(0, 0, 0, 0.602));
          filter: drop-shadow(-200px 290px 130px rgba(0, 0, 0, 0.602));
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
}

.header {
  display: flex;
  align-items: center;
  justify-content: left;
  flex-direction: row;
  height: 10vh;
  width: 100vw;
  font-family: 'Outfit', sans-serif;
  font-weight: bold;
  font-size: xx-large;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: rgb(245, 226, 253);
}
.description {
  font-weight: 300;
  font-style: italic;
}
.header-item-sleep {
  margin-left: auto;
  padding-right: 50px;
  font-weight: 100;
  opacity: 50%;
}
.header-item-woke {
  margin-left: auto;
  padding-right: 50px;
  font-weight: 100;
  opacity: 100%;
}

.tracy {
  margin: 30px;
  padding: 10px;
  /* border-radius: 100px;
  border-style: none;
  box-shadow: rgba(0, 0, 0, 0.2) 0 3px 5px -1px,
    rgba(0, 0, 0, 0.14) 0 6px 10px 0, rgba(0, 0, 0, 0.12) 0 1px 18px 0; */
}

.content {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
  font-family: 'Outfit', sans-serif;
  font-weight: bold;
  font-size: 50px;
  color: rgb(245, 226, 253);
}

.overlay {
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0px;
}

